.AccordeonInput{position:absolute;opacity:0;z-index:-1}.AccordeonContainer{border-radius:8px;margin-left:auto;margin-right:auto;box-shadow:0 4px 4px -2px rgba(0,0,0,.5);background:rgba(255,255,255,.2);margin-bottom:50px;box-shadow:0 4px 30px rgba(0,0,0,.1);backdrop-filter:blur(5px);-webkit-backdrop-filter:blur(5px)}.AccordeonTab{width:100%;color:#fff;overflow:hidden}.TabLabel{display:flex;justify-content:space-between;padding:1em;font-weight:bold;cursor:pointer;border:1px solid rgba(255,255,255,.5)}.TabLabel::after{content:"❯";width:1em;height:1em;text-align:center;transition:all .35s}.TabContent{max-height:0;padding:0 1em;color:#c61818;transition:all .35s;text-shadow:rgba(255,255,255,.425) 0px 54px 55px,rgba(255,255,255,.329) 0px -8px 30px,rgba(255,255,255,.411) 0px 4px 6px,rgba(255,255,255,.336) 0px 12px 13px,rgba(255,255,255,.281) 0px -3px 5px}.AccordeonInput:checked+.TabLabel::after{transform:rotate(90deg)}.AccordeonInput:checked~.TabContent{max-height:67vh;padding:1em}.FightResult{text-align:center;font-family:"PT Serif","serif";text-shadow:rgba(255,255,255,.425) 0px 54px 55px,rgba(255,255,255,.329) 0px -8px 30px,rgba(255,255,255,.411) 0px 4px 6px,rgba(255,255,255,.336) 0px 12px 13px,rgba(255,255,255,.281) 0px -3px 5px}.TeamScoreFigure{font-size:40px;font-weight:700;text-align:center;margin-top:10px;text-shadow:rgba(255,255,255,.425) 0px 54px 55px,rgba(255,255,255,.329) 0px -8px 30px,rgba(255,255,255,.411) 0px 4px 6px,rgba(255,255,255,.336) 0px 12px 13px,rgba(255,255,255,.281) 0px -3px 5px}